import React, { Component } from "react";

export default class About extends Component {
  render() {
    const { about, aboutStrings } = this.props;
    return (
      <section id="about">
        <div className="row">
          <div className="two columns">
            <img className="profile-pic" src="images/profile.jpeg" alt="" />
          </div>
          <div className="ten columns main-col">
            <h2>{aboutStrings.aboutMe}</h2>
            <p className="aboutMe">{about.aboutMe}</p>
            <p className="aboutMeEmphasized">{about.aboutMeEmphasized}</p>
          </div>
        </div>
      </section>
    );
  }
}
