import React, { Component, Fragment } from "react";
import accordion from "../accordion";

export default class Resume extends Component {
  getEducationData() {
    const {
      resume: { education },
    } = this.props;
    let educationData = Object.keys(education).map((key) => {
      return education[key];
    });
    let formattedEducationData = educationData.map((obj, index) => {
      let data = (
        <Fragment key={index}>
          <div className="row item">
            <div className="twelve columns">
              <h3>
                {obj.universityOrSchool},{obj.place}
              </h3>
              <p className="info">
                {obj.degree} <span>•</span>
                <em className="date">
                  {obj.yearStart}-{obj.yearEnd}
                </em>
                <span>•</span>
                <em>
                  {obj.marksType}-{obj.cgpaOrPercentage}
                </em>
              </p>
              {obj.achievements.map((achievement, index) => {
                return <p key={index}>{achievement}</p>;
              })}
            </div>
          </div>
        </Fragment>
      );
      return data;
    });
    return formattedEducationData;
  }
  getSkillsData(type) {
    const {
      resume: { skills },
    } = this.props;
    const skillsData = skills[type];
    const formattedskillsData = (
      <div className="bars">
        <ul className="skills">
          {skillsData.map((obj, index) => {
            return (
              <li key={index}>
                <span className={`bar-expand ${obj.formatter}`} />
                <em>{obj.data}</em>
              </li>
            );
          })}
        </ul>
      </div>
    );
    return formattedskillsData;
  }
  getWorkData() {
    const {
      resume: {
        professionalWork: { companies },
      },
    } = this.props;
    const allProjects = [];
    Object.keys(companies).forEach((key) => {
      companies[key].forEach((proj) => {
        allProjects.push(proj);
      });
    });
    let formattedData = allProjects.map((company, index) => {
      return (
        <div className="row item" key={index}>
          <div className="twelve columns">
            <h3>{company.projectName}</h3>
            <p className="info">
              {company.companyName}
              <span>•</span>
              {company.designation} <span>•</span>
              {company.domain}
              <span>•</span>
              <em className="date">
                {company.yearStart}-{company.yearEnd}
              </em>
            </p>
            <p>{company.projectDetails}</p>
          </div>
        </div>
      );
    });
    return formattedData;
  }
  getBarChartTables() {
    const {
      resumeStrings: { skillsStrings },
    } = this.props;
    return [
      {
        heading: "Languages and JS frameworks",
        body: (
          <table className="tablebars">
            <thead>
              <tr>
                <th>{skillsStrings.languages}</th>
                <th>{skillsStrings.frameworks}</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{this.getSkillsData("languages")}</td>
                <td>{this.getSkillsData("jsFrameworks")}</td>
              </tr>
            </tbody>
          </table>
        ),
      },
      {
        heading: "Java, C# frameworks, and databases",
        body: (
          <table className="tablebars">
            <thead>
              <tr>
                <th>{skillsStrings.frameworks}</th>
                <th>{skillsStrings.databases}</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{this.getSkillsData("frameworks")}</td>
                <td>{this.getSkillsData("dbms")}</td>
              </tr>
            </tbody>
          </table>
        ),
      },
      {
        heading: "Design patterns and testing frameworks",
        body: (
          <table className="tablebars">
            <thead>
              <tr>
                <th>{skillsStrings.designPatterns}</th>
                <th>{skillsStrings.testingframeworks}</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{this.getSkillsData("designPatterns")}</td>
                {this.getSkillsData("testing")}
              </tr>
            </tbody>
          </table>
        ),
      },
      {
        heading: "Editors and ORM's",
        body: (
          <table className="tablebars">
            <thead>
              <tr>
                <th>{skillsStrings.editors}</th>
                <th>{skillsStrings.objectRelationalMappingFrameworks}</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{this.getSkillsData("ide")}</td>

                <td>{this.getSkillsData("orm")}</td>
              </tr>
            </tbody>
          </table>
        ),
      },
      {
        heading: "Container system, their orchestration and version controls",
        body: (
          <table className="tablebars">
            <thead>
              <tr>
                <th>{skillsStrings.containers}</th>
                <th>{skillsStrings.sourceControls}</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{this.getSkillsData("containers")}</td>

                <td>{this.getSkillsData("sourceControl")}</td>
              </tr>
            </tbody>
          </table>
        ),
      },
      {
        heading: "Project tracking",
        body: (
          <table className="tablebars">
            <thead>
              <tr>
                <th>{skillsStrings.tools}</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{this.getSkillsData("projectTracking")}</td>
                <td></td>
              </tr>
            </tbody>
          </table>
        ),
      },
    ];
  }
  getAccordion() {
    const data = this.getBarChartTables();
    return accordion({items:data});
  }
  render() {
    const {
      resume: { skills },
      resumeStrings,
    } = this.props;
    return (
      <>
        <section id="resume">
          <section id="skills">
            <div className="row skill">
              <div className="two columns header-col">
                <h1>
                  <span>{resumeStrings.skills}</span>
                </h1>
              </div>
              <div className="ten columns main-col">
                <p>{skills.description}</p>
                <br />
                {this.getAccordion()}
              </div>
            </div>
          </section>
          <br/>
          <br/>
          <section id="education">
            <div className="row education">
              <div className="two columns header-col">
                <h1>
                  <span>{resumeStrings.education}</span>
                </h1>
              </div>
              <div className="ten columns main-col">
                {this.getEducationData()}
              </div>
              {/* main-col end */}
            </div>
          </section>
          <section id="professional-work">
            <div className="row work">
              <div className="two columns header-col">
                <h1>
                  <span>{resumeStrings.work}</span>
                </h1>
              </div>
              <div className="ten columns main-col">{this.getWorkData()}</div>
              {/* main-col end */}
            </div>
          </section>
        </section>
      </>
    );
  }
}
