import React, { Component } from "react";
export default class Header extends Component {
  render() {
    const {
      resumeData: { header },
      headerStrings,
      about,
      aboutStrings,
    } = this.props;
    return (
      <React.Fragment>
        <header id="home">
          <nav id="nav-wrap">
            <a className="mobile-btn" href="#nav-wrap" title="Show navigation">
              {headerStrings.showNavigation}
            </a>
            <a className="mobile-btn" href="#" title="Hide navigation">
              {headerStrings.hideNavigation}
            </a>
            <ul id="nav" className="nav">
              <li className="current">
                <a className="smoothscroll" href="#home">
                  {headerStrings.home}
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#about">
                  {headerStrings.about}
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#skills">
                  {headerStrings.skills}
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#education">
                  {headerStrings.education}
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#professional-work">
                  {headerStrings.professionalWork}
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#portfolio">
                  {headerStrings.works}
                </a>
              </li>
              <li>
                <a className="smoothscroll" href="#contact">
                  {headerStrings.contact}
                </a>
              </li>
              <li>
                <a
                  href={`/downloads/${about.fileName}`}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                {aboutStrings.download}
                </a>
              </li>
            </ul>
            {/* end #nav */}
          </nav>
          {/* end #nav-wrap */}
          <div className="row banner">
            <div className="banner-text">
              <h1 className="responsive-headline">{header.headline}</h1>
              <h3>{header.headlinePostScript}</h3>
              <hr />
              <ul className="social">
                <li>
                  <a
                    href={header.github}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <i className="fa fa-github" />
                  </a>
                </li>
                <li>
                  <a
                    href={header.linkedin}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <i className="fa fa-linkedin" />
                  </a>
                </li>
                <li>
                  <a
                    href={header.stackExchange}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <i className="fa fa-stack-exchange" />
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <p className="scrolldown">
            <a className="smoothscroll" href="#about">
              <i className="icon-down-circle" />
            </a>
          </p>
        </header>
      </React.Fragment>
    );
  }
}
