import React, { Component } from "react";

export default class Contactus extends Component {
  render() {
    const { contactus, contactusStrings } = this.props;
    return (
      <section id="contact">
        <div className="row section-head">
          <div className="two columns header-col"></div>
          <div className="ten columns">
            <h1>Get In Touch</h1>
            <pre>
              <p className="lead">
                {contactusStrings.phoneNo} {contactus.phoneNo}{" "}
                {contactusStrings.email} {contactus.email}
              </p>
            </pre>
          </div>
        </div>
        <div className="row">
          <div className="eight columns"></div>
        </div>
      </section>
    );
  }
}
