import React, { Component } from 'react'

export default class Portfolio extends Component {

    getModal() {
        const { portfolio: { projects }, portfolioStrings } = this.props;
        return (
            projects.map((project, index) => {
                return <div id={`modal-0${index}`} className="popup-modal mfp-hide" key={index}>
                    <img
                        className="scale-with-grid"
                        src={`images/portfolio/${project.image}`}
                        alt=""
                    />
                    <div className="description-box">
                        <h4>{project.name}</h4>
                        <p>
                            {project.description}
                        </p>
                        <span className="categories">
                            <i className="fa fa-tag" />
                            {project.branding}
                        </span>
                    </div>
                    <div className="link-box">
                        <a href={project.url} target="_blankc">{portfolioStrings.details}</a>
                        <a className="popup-modal-dismiss">{portfolioStrings.close}</a>
                    </div>
                </div>
            })
        );
    }
    getPortfolioItem() {
        const { portfolio: { projects } } = this.props;
        return (projects.map((project, index) => {
            return <div className="columns portfolio-item" key={index}>
                <div className="item-wrap">
                    <a href={`#modal-0${index}`} data="title">
                        <img alt={project.name} src={`images/portfolio/${project.image}`} />
                        <div className="overlay">
                            <div className="portfolio-item-meta">
                                <h5>{project.name}</h5>
                                <p>{project.type}</p>
                            </div>
                        </div>
                        <div className="link-icon"><i className="icon-plus" /></div>
                    </a>
                </div>
            </div>
        }));
    }
    render() {
        const { portfolioStrings } = this.props;
        return (
            <section id="portfolio">
                <div className="row">
                    <div className="twelve columns collapsed">
                        <h1>{portfolioStrings.heading}</h1>
                        {/* portfolio-wrapper */}
                        <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                            {this.getPortfolioItem()}
                        </div> {/* portfolio-wrapper end */}
                    </div> {/* twelve columns end */}
                    {/* Modal Popup--------------------------------------------------------------- */}
                    {this.getModal()}
                    {/* modal- End */}

                </div> {/* row End */}
            </section>
        )
    }
}
